function TransactionContainer ({ transaction }) {
    return (
        <div className="transaction-container">
            <div className="transaction-container__paragraph">: {transaction.date}</div>
            <div className="transaction-container__paragraph">: {transaction.category}</div>
            <div className="transaction-container__paragraph">: {transaction.location}</div>
            <div className="transaction-container__paragraph">   : {transaction.amountForeign} {transaction.currencyForeign}</div>
            <div className="transaction-container__paragraph">   : {transaction.amountLocal} {transaction.currencyLocal}</div>
        </div>
    )
}

export default TransactionContainer;