function GoalContainer ({ goal }) {
    return (
        <div className="goal-container">
            <div className="goal-container__paragraph">: {goal.description}</div>
            <div className="goal-container__paragraph">: {goal.amount} {goal.currency}</div>
            <div className="goal-container__paragraph">: {goal.deadline}</div>
        </div>
    )
}

export default GoalContainer;